@media (max-width: 750px) {
    .navcontainer {
    flex-flow: row;
    align-items: center;
    }

    .menu {
    font-size: 16px;
    width: 50%;
    flex-direction: column;
    align-items: flex-end;
    margin: 16px;
    font-size: 16px;
    }

    .menu a {
    padding: 8px;
    }

    .logo {
    display: flex;
    width: 130px;
    padding: 16px;
    margin: 0;
    }
}